<template>
    <div class="musicList">
        <div class="musicListTop">
            <div class="title">发现好歌单</div>
            <div class="more">发现更多</div>
        </div>
        <div class="musicListMain">
            <div class="swiper-container" id="musicListSwiper">
                <div class="swiper-wrapper">
                    <router-link  :to="{path: '/musiclistview', query:{id: item.id}}" class="swiper-slide" v-for="(item, i) in state.musicListArr" :key="i">
                        <img :src="item.picUrl" :alt="item.name">
                        <span class="musicListName">{{item.name}}</span>
                        <span class="musicListPlayCount">
                            <i class="iconfont icon-24gl-play"></i>
                            {{transferNum(item.playCount)}}
                        </span>
                    </router-link>
                </div>
            </div>
        </div>    
    </div>
</template>

<script>
import 'swiper/css/swiper.css'
import Swiper from 'swiper'
import { getMusicList } from '@/api'
import { onMounted, onUpdated, reactive } from '@vue/runtime-core'

export default {
    name: 'MusicList',
    setup(){
        function transferNum(num){
            let res = num
            if(num >= 100000000 ){
                res = num/100000000
                res = res.toFixed(2) + "亿"
            }else if(num >= 10000){
                res = num/10000
                res = res.toFixed(2) + "万"
            }
            return res
        }

        let state = reactive({musicListArr:[]})
        onMounted(async () => {
            let res = await getMusicList(9)
            state.musicListArr = res.data.result
        })

        onUpdated(() => {
            var mySwiper = new Swiper("#musicListSwiper", {
                loop: false,
                slidesPerView: 3,
                spaceBetween: 10,
                // pagination: {
                //     el: ".swiper-pagination",
                //     clickable: true,
                // },
            });
        })
        return {
            state,
            transferNum
        }
    }
}
</script>

<style lang="less" scoped>
.musicList{
    width: 7.5rem;
    padding: .2rem;
    padding-top: 0;
    .musicListTop{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .2rem 0;
        .title{
            font-size: .35rem;
            font-weight: 900;
        }
        .more{
            border: 1px solid #ccc;
            padding: .12rem .26rem;
            border-radius: .4rem;
            font-size: .2rem;
        }
    }
    .musicListMain{
        .swiper-container{
            width: 100%;
            height: 3rem;
            .swiper-slide{
                // background-color: orange;
                display: flex;
                flex-direction: column;
                position: relative;
                overflow: hidden;
                img{
                    width: 100%;
                }
                .musicListName{
                    height: 0.9rem;
                    width: 100%;
                    font-size: .27rem;
                }
                .musicListPlayCount{
                    position: absolute;
                    right: 0.1rem;
                    top: 0.1rem;
                    font-size: 0.25rem;
                    color: rgb(255, 255, 255);
                }
            }
            .swiper-slide:nth-child(2) img{
                display: block;
                font-size: 0;
            }
        }
    }
}
</style>