<template>
    <div id="swiperCom">
        <div class="swiper-container" id="swiperComSwiper">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,i) in imgArr" :key="i">
                    <a href="">
                        <img :src="item.pic">
                    </a>
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>
            <!-- 如果需要导航按钮 -->
            <!-- <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div> -->
            
            <!-- 如果需要滚动条 -->
            <!-- <div class="swiper-scrollbar"></div> -->
        </div>
    </div>
</template>

<script>
import 'swiper/css/swiper.css'
import Swiper from 'swiper'
import {getBanner} from '@/api'

export default {
    data(){
        return{
            imgArr: []
        }
    },
    async mounted(){
        let res = await getBanner(1)
        this.imgArr = res.data.banners
    },
    updated(){
        var mySwiper = new Swiper ('#swiperComSwiper', {
            loop: true, // 循环模式选项
            
            // 如果需要分页器
            pagination: {
                el: '.swiper-pagination',
            },
            
            // 如果需要前进后退按钮
            // navigation: {
            // nextEl: '.swiper-button-next',
            // prevEl: '.swiper-button-prev',
            // },

            autoplay: {
                delay: 2500,
                disableOnInteraction: false,
            },
            
            // 如果需要滚动条
            scrollbar: {
                el: '.swiper-scrollbar',
            },
        })
    }
}
</script>

<style lang="less">
#swiperCom{
    width: 7.5rem;   
    .swiper-container{
        width: 7.1rem;
        height: 2.9rem;
        
        .swiper-pagination-bullet-active{
            background: orangered;
        }
    }
    img{
        width: 100%;
    }
}
</style>